@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
*{box-sizing:border-box;margin:0;padding:0}*::before,*::after{box-sizing:border-box;margin:0;padding:0}:focus{outline:none}button{text-decoration:none;color:inherit;cursor:pointer;background-color:transparent;border:transparent;font-size:16px}nav{display:flex;flex-wrap:wrap;background-color:teal;padding:0 10% 10px;justify-content:space-between;box-shadow:0 0 10px #00000077;-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px)}.logo{font-family:'Pacifico', cursive;font-size:2rem;transition:color 0.3s ease}.logo:hover,.logo:focus{color:#111}ul{list-style:none;display:flex;align-items:center}li{margin-left:2em}.link{position:relative;text-decoration:none;color:black;font-family:'Roboto', sans-serif;font-size:16px}.link::after{content:'';display:block;position:absolute;background-color:#444;width:100%;height:0.2em;bottom:-0.3em;left:0;border-radius:0.1em;transform:scaleX(0);transition:transform 0.3s ease;transform-origin:right}.link:hover::after,.link:focus::after{transform:scaleX(1);transform-origin:left}.menu-label,.menu-button{display:none}.username{font-size:16px}@media screen and (max-width: 600px){.menu-label{display:inline;align-self:center;cursor:pointer}ul{margin:0.5em 0;display:none;flex:100% 1}li{margin-left:0;text-align:center;margin-bottom:1em}#toggle:checked+ul{display:block}}

.page-container{display:flex;flex-direction:column;min-height:100vh}.content-wrap{flex:1 1}

body{font-family:'Roboto', sans-serif}

